<template>
    <div
        id="product-review"
        class="product-container container-fluid"
        v-bind:class="{ 'fix-product': scrollPosition > 0 }"
    >
        <div class="product-container container">
            <div class="product-details col-lg-9 col-12">
                    <div class="product-photo col-lg-5 col-md-6 col-12">
                    <img id="main_img" :src="'https://queen-store.net/azshop/public_html/board/public_html/items-photos/' +  item.photo_link"  @error="replaceByDefault" srcset="" />
                    <div class="sub-ima">
                        <div class="f1_img" v-for="photo in photos" :key="photo.index" @click="setImg(photo)">
                            <img :src="'https://queen-store.net/azshop/public_html/board/public_html/' +  photo" alt="" srcset="" />
                        </div>
                        
                    </div>
                </div>
                <div class="product-info col-lg-7 col-md-6">
                    <span class="product-category">{{item.category}}</span>
                    <h3>{{item.name}}</h3>
                    <ul>
                        <li class="item-price">
                            <span v-if="item.discount" class="old-price"> {{((Math.round(parseFloat(item.selling_price/((100-item.discount)))*100)))}}  </span>
                               {{(item.selling_price)}}  L.E
                        </li>
                        <li class="product-rating">
                            <star-rating
                                :increment="0.5"
                                active-color="#00a1e8"
                                star-size="20"
                                :rating="item.rate"
                                v-bind:read-only="true"
                            ></star-rating>
                        </li>
                        <li class="product-details-p">
                            <p>
                                {{item.description}}
                            </p>
                        </li>

                        <li class="product-availabilty">
                            <span>
                                availability:
                            </span>
                            in stock
                        </li>
                        <li class="product-code">
                            <span>
                                product code :
                            </span>
                            {{item.code}}
                        </li>
                        <li class="product-code d-none">
                            <span>
                                quantity :
                            </span>
                            {{ item.quantity }}
                        </li>
                        <li class="product-size">
                            <span>
                                size:
                            </span>
                            <select name="sizes" id="size-select" @change="getItemSize($event)">
                                <option v-for="size in sizes" :key="size" :value="size">{{size}}</option>
                            </select>
                        </li>
                        <li class="product-quantity">
                            <span>quantity:</span>
                            <button class="minus" @click="decrementQuantity">
                                -
                            </button>
                            <input
                                type="number"
                                name="quantity"
                                v-model="selectedQuantity"
                            />
                            <button class="plus" @click="incrementQuantity">
                                +
                            </button>
                        </li>
                        <li class="add-to-cart">
                           
                            <button v-if="this.user_token!='null'" @click="addItemToCart()" >
                                <font-awesome-icon
                                    :icon="['fas', 'shopping-basket']"
                                />
                                add to cart
                            </button>
                             <button v-else @click="TrigLogin()" >
                                <font-awesome-icon
                                    :icon="['fas', 'shopping-basket']"
                                />
                                add to cart
                            </button>
                        </li>
                    </ul>
                </div>
            </div>
            <div class="product-colors col-lg-3 col-12">
                <div class="product-colors-head">
                    choose color
                </div>
                <div class="product-colors-sec" style="background:#f0f9fb">
                    <ul>
                        <li
                            v-for="color in colors"
                            :key="color.num"
                            :class="color.state"
                        >
                            <span
                                :data-color="color.color"
                                @click="selectcolor($event, color.num)"
                                :style="{ '--pro-color': color.color }"
                            >
                            </span>
                            <div class="check-icon">
                                <font-awesome-icon :icon="['fas', 'check']" />
                            </div>
                        </li>
                    </ul>
                </div>
            </div>
        </div>
        <div class="related-products-container container-fluid d-none">
            <div class="related-products container">
                <div class="related-product-header">
                    <div class="header">
                        <div class="header-title">
                            
                        
                        <span>related products</span>
                        </div>
                    </div>
                    <div class="related-products-sec items">
                        <Item
                            v-for="item in items"
                            v-bind:key="item.id"
                            :views="item.views"
                            :productname="item.productname"
                            :category="item.category"
                            :sale="item.sale"
                            :oldprice="item.oldprice"
                            :currentprice="item.currentprice"
                            :currentrating="item.currentrating"
                            :rate="item.rate"
                            :ratingselected="item.ratingselected"
                            :selectedrating="item.selectedrating"
                        />
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<style lang="scss">
@import "../style/css/scss/views/_product.scss";

</style>

<script>

import axios from "axios";

import StarRating from "vue-star-rating";
import Item from "../components/global/item.vue";

export default {
    name: "product",
    props:["login"],


    components: {
        StarRating,
        Item
    },
    el: "#product-review",

    data() {
        return {
            scrollPosition: null,
            productQuantity: "55",
            selectedQuantity: "1",
            item_id:this.$route.params.itemId,
            item:{},
            colors: [
                
            ],
            selectedColor: "",
            user_token:"",
            logged:"",
            selectedSize:"",
            sizes:[],
            photos:[]

        };
    },
    methods: {
        replaceByDefault(e){
            e.target.src = "https://queen-store.net/azshop/public_html/board/public_html/" + this.photos[0];

        },
        TrigLogin(){
            document.getElementById("log").click();

        },
        updateScroll() {
            this.scrollPosition = window.scrollY;
        },
        selectcolor: function(event, index) {
            this.sizes = [];
            for (var i = 0; i < this.colors.length; i++) {
                if (i+1 == index) {
                    this.colors[i].state = "active";
                    this.selectedColor = event.target.getAttribute(
                        "data-color"
                    );
                    this.sizes.push(this.colors[i].sizes);
                } else {
                    this.colors[i].state = "not-active";
                }
            }
        },
        getItemSize(event){
            this.selectedSize = event.target.value
        },
        decrementQuantity: function() {
            if (this.selectedQuantity > 1) {
                this.selectedQuantity--;
            }
        },
        incrementQuantity: function() {
            
            if (this.selectedQuantity < this.productQuantity) {
                this.selectedQuantity++;
            }
        },
        addItemToCart(){
            if(this.selectedColor==""){
                alert("Please select A Color");
                
                return ;
            }
            this.user_token = localStorage.getItem("user_token");
            axios
            .get(
                'https://queen-store.net/azshop/public_html/board/public_html/api/storecart', 
                {
                    headers: {'Authorization': 'Bearer ' + this.user_token },
                    params:{
                        items_ids:[this.item_id],
                        quantity:[this.selectedQuantity],
                        color:[this.selectedColor],
                        size:[this.selectedSize]
                    }
                }
            ).then(()=>{
                this.$router.push("/products");
                location.reload();
            })
        },
        setImg(e){
 	
            var m_img = document.getElementById("main_img");
	
            m_img.setAttribute("src" , "https://queen-store.net/azshop/public_html/board/public_html/" + e);

        }
    },
    mounted() {
          window.scrollTo(0, 0)

        this.user_token = localStorage.getItem("user_token");
        var counter = 0;
      //  window.addEventListener("scroll", this.updateScroll);
        
        axios.get(
            ('https://queen-store.net/azshop/public_html/board/public_html/api/items/'+this.item_id),
        ).then((res) => {
            this.item = res.data
            this.sizes = res.data.sizes;
            this.selectedSize = res.data.sizes[0]
            this.productQuantity = res.data.quantity
            this.photos = res.data.photos
            this.logged = res.data.user;

            var i = 0;
            for (i ; i < this.item.colors.length ; i++){
                for(var x = 0 ; x < this.item.colors[i].length ; x++){
                    counter = counter +1;
                    this.colors.push({num: counter,color: this.item.colors[i][x].color , color_code: this.item.colors[i][x].color_code, sizes : this.item.colors[i][x].size,state: "not-active"});
                }
            }
        })
    }
};
</script>
